@import url("https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;0,400;0,600;1,300;1,400;1,600&display=swap");

* {
  font-family: "Open Sans", sans-serif;
}
.main {
  max-width: 600px;
  margin: 10px auto;
}

.createnote {
  max-width: 400px;
  margin: 10px auto;
}

.box-card {
  border-radius: 10px !important;
}

form {
  max-width: 350px;
  margin: auto;
}

.createBtn span {
  color: white;
}

.el-textarea__inner:focus {
  outline: 0;
  border-color: #f21170 !important;
}
.el-input.is-active .el-input__inner,
.el-input__inner:focus {
  outline: 0;
  border-color: #f21170 !important;
}

.router-link {
  text-decoration: none;
  color: white;
}

.box-note {
  border-radius: 10px !important;
  margin-top: 20px;
  margin-bottom: 20px;
  .note-title {
    display: flex;
    border-left: 5px solid #867ae9;
    justify-content: space-between;
    align-items: center;
  }
  h3 {
    text-align: start;
    margin-left: 10px;
  }
}


@media (max-width: 475px) {
    .box-note {
        h3 {
          text-align: start;
          margin-left: 10px;
          font-size: 15px;
        }
      }
}